import React, { useState, useEffect } from "react";

const Calculator = () => {
  const [FirstValue, setFirstValue] = useState("");
  const [SecondValue, setSecondValue] = useState("");
  const [SelectValue, setSelectValue] = useState("+");
  const [Result, setResult] = useState("Result");

  const Change = (e) => {
    setSelectValue(e.target.value);
  };

  const Calculate = () => {
    if (SelectValue === "-") {
      setResult(FirstValue - SecondValue);
    } else if (SelectValue === "*") {
      setResult(FirstValue * SecondValue);
    } else if (SelectValue === "/") {
      setResult(FirstValue / SecondValue);
    } else {
      setResult(FirstValue + SecondValue);
    }
  };

  useEffect(() => {
    Calculate();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [FirstValue, SecondValue, SelectValue]);

  return (
    <div className="calculator">
      <h1>Simple Calculator</h1>
      <input
        type="number"
        required
        value={FirstValue.toString()}
        onChange={(e) => setFirstValue(parseInt(e.target.value))}
      />
      <select onChange={Change}>
        <option value="+">+</option>
        <option value="-">-</option>
        <option value="*">*</option>
        <option value="/">/</option>
      </select>
      <input
        type="number"
        value={SecondValue.toString()}
        onChange={(e) => setSecondValue(parseInt(e.target.value))}
      />
      {isNaN(Result) ? (
        <p className="emptyErr">All Fields Required*</p>
      ) : (
        <p className="result">{Result.toString()}</p>
      )}
    </div>
  );
};

export default Calculator;
