import { Switch, Route } from "react-router-dom";
import { createContext, useEffect, useReducer } from "react";

import { AnimatePresence } from "framer-motion";
//Navbar, Footer

import Footer from "./components/Footer";
import Navbar from "./components/Navbar";
//pages
import Home from "./components/pages/Home";
import About from "./components/pages/About";
import Contact from "./components/pages/Contact";
import Err404 from "./components/pages/Err404";
import Register from "./components/pages/Register";
import Login from "./components/pages/Login";
import Logout from "./components/pages/Logout";
import PrivacyPolicy from "./components/pages/PrivacyPolicy";
import TermsServices from "./components/pages/TermsServices";
import Covid from "./components/pages/Projects/Covid";
import Projects from "./components/pages/Projects/Projects";
import Weather from "./components/pages/Projects/Weather";
import MathPlayground from "./components/pages/MathPlayground/MathPlayground";

import { initialState, reducer } from "./Reducer";
import axios from "./axios";

export const UserContext = createContext();

const App = () => {
  const [state, dispatch] = useReducer(reducer, initialState);

  const getData = async () => {
    try {
      const res = await axios.get("/navbar");
      if (res) {
        dispatch({ type: "LoggedIn", payload: true });
      }
    } catch (error) {
      console.log(error);
    }
  };

  useEffect(() => {
    getData();
  }, []);

  return (
    <>
      <UserContext.Provider value={{ state, dispatch }}>
        <Navbar />

        <div className="main_div" style={{ minHeight: window.innerHeight }}>
          <AnimatePresence exitBeforeEnter>
            <Switch location={window.location} key={window.location.pathname}>
              <Route exact path="/">
                <Home />
              </Route>
              <Route path="/contact">
                <Contact />
              </Route>
              <Route path="/about">
                <About />
              </Route>
              <Route path="/logout">
                <Logout />
              </Route>
              <Route path="/signup">
                <Register />
              </Route>
              <Route path="/login">
                <Login />
              </Route>
              <Route exact path="/projects">
                <Projects />
              </Route>

              <Route path="/mathplayground">
                <MathPlayground />
              </Route>

              {/* projects start */}
              <Route path="/projects/covid">
                <Covid />
              </Route>
              <Route path="/projects/weather">
                <Weather />
              </Route>
              {/* projects end */}

              <Route path="/privacy-policy">
                <PrivacyPolicy />
              </Route>
              <Route path="/terms-services">
                <TermsServices />
              </Route>
              <Route component={Err404} />
            </Switch>
          </AnimatePresence>
        </div>

        <Footer />
      </UserContext.Provider>
    </>
  );
};

export default App;
