import React, { useState, useEffect } from "react";
import { motion } from "framer-motion";
import { pageTransitions, pageVariants } from "../Home";

import "../../css/Covid.css";

const Covid = () => {
  const [data, setData] = useState([]);

  const getData = async () => {
    const res = await fetch("https://api.covid19india.org/data.json");
    const data = await res.json();
    setData(data.statewise);
  };

  useEffect(() => {
    getData();
  }, []);

  return (
    <motion.div
      className="container my-5"
      initial="initial"
      animate="in"
      exit="out"
      transition={pageTransitions}
      variants={pageVariants}
    >
      <h1 className="my-5 text-center covid-heading">
        COVID - 19 Live Statewise Cases (INDIA)
      </h1>
      <div className="covid_container">
        <table className="table table-light table-hover">
          <thead>
            <tr>
              <th>State</th>
              <th>Active</th>
              <th>Confirmed</th>
              <th>Recovered</th>
              <th>Deaths</th>
              <th>Last Update Time</th>
            </tr>
          </thead>
          <tbody>
            {data.map((all, ind) => {
              return (
                <tr key={ind}>
                  <td>{all.state}</td>
                  <td>{all.active}</td>
                  <td>{all.confirmed}</td>
                  <td>{all.recovered}</td>
                  <td>{all.deaths}</td>
                  <td>{all.lastupdatedtime}</td>
                </tr>
              );
            })}
          </tbody>
        </table>
      </div>
    </motion.div>
  );
};

export default Covid;
