import { useContext, useEffect } from "react";
import { NavLink } from "react-router-dom";
import { UserContext } from "../App";

import { Scroll } from "./Footer";

const RemoveClass = () => {
  Scroll();
  const navbar = document.querySelector(".collapse");
  navbar.classList.remove("show");
};

const Navbar = () => {
  const { state } = useContext(UserContext);

  window.addEventListener("scroll", () => {
    const nav = document.querySelector("#navigation_bar");
    nav.classList.toggle("sticky", window.scrollY > 0);
  });

  return (
    <div className="container-fluid navigation_bar" id="navigation_bar">
      <div className="row">
        <div className="col-md-10 col-12 mx-auto">
          <nav className="navbar navbar-expand-lg navbar-dark">
            <div className="container-fluid">
              <NavLink className="navbar-brand" to="/" onClick={RemoveClass}>
                MrSaaD
              </NavLink>
              <button
                className="navbar-toggler"
                type="button"
                data-bs-toggle="collapse"
                data-bs-target="#navbarSupportedContent"
                aria-controls="navbarSupportedContent"
                aria-expanded="false"
                aria-label="Toggle navigation"
              >
                <span className="navbar-toggler-icon"></span>
              </button>
              <div
                className="collapse navbar-collapse"
                id="navbarSupportedContent"
              >
                <ul className="navbar-nav ms-auto mb-2 mb-lg-0">
                  <li className="nav-item">
                    <NavLink
                      exact
                      className="nav-link"
                      onClick={RemoveClass}
                      aria-current="page"
                      to="/"
                      activeClassName="border-b"
                    >
                      Home
                    </NavLink>
                  </li>

                  <li className="nav-item">
                    <NavLink
                      className="nav-link"
                      onClick={RemoveClass}
                      activeClassName="border-b"
                      to="/contact"
                    >
                      Contact
                    </NavLink>
                  </li>
                  {state ? (
                    <>
                      <li className="nav-item">
                        <NavLink
                          className="nav-link"
                          onClick={RemoveClass}
                          activeClassName="border-b"
                          to="/about"
                        >
                          About
                        </NavLink>
                      </li>
                      <li className="nav-item">
                        <NavLink
                          className="nav-link"
                          onClick={RemoveClass}
                          activeClassName="border-b"
                          to="/projects"
                        >
                          Projects
                        </NavLink>
                      </li>
                      <li className="nav-item">
                        <NavLink
                          className="nav-link"
                          onClick={RemoveClass}
                          activeClassName="border-b"
                          to="/mathplayground"
                        >
                          Math Playground
                        </NavLink>
                      </li>

                      <span className="nav-item">
                        <NavLink
                          className="nav-link mt-1 mt-md-0 ms-md-2  bg-danger d-inline-flex p-2 rounded-3"
                          onClick={RemoveClass}
                          activeClassName="border-b"
                          to="/logout"
                        >
                          Logout
                        </NavLink>
                      </span>
                    </>
                  ) : (
                    <>
                      <li className="nav-item">
                        <NavLink
                          className="nav-link"
                          onClick={RemoveClass}
                          activeClassName="border-b"
                          to="/signup"
                        >
                          SignUp
                        </NavLink>
                      </li>
                      <li className="nav-item">
                        <NavLink
                          className="nav-link"
                          onClick={RemoveClass}
                          activeClassName="border-b"
                          to="/login"
                        >
                          Login
                        </NavLink>
                      </li>
                    </>
                  )}
                </ul>
              </div>
            </div>
          </nav>
        </div>
      </div>
    </div>
  );
};

export default Navbar;
