import React, { useContext } from "react";
import { Link, useHistory } from "react-router-dom";
import "../../css/Projects.css";
import { motion } from "framer-motion";
import { pageTransitions, pageVariants } from "../Home";
import { UserContext } from "../../../App";

const List = [
  {
    id: 0,
    title: "COVID - 19 Live Tracker (INDIA)",
    url: "/projects/covid",
    image: "images/covid.jpg",
  },
  {
    id: 1,
    title: "Weather App - Quick Answer To What Temperature Is Going On",
    url: "/projects/weather",
    image: "images/weather.png",
  },
];

const Projects = () => {
  const { state } = useContext(UserContext);
  const history = useHistory();

  if (!state) {
    history.push("/login");
  }
  return (
    <>
      <motion.div
        className="container main_projects_container"
        initial="initial"
        animate="in"
        exit="out"
        transition={pageTransitions}
        variants={pageVariants}
      >
        <h1 className="mb-3 text-center">Projects</h1>
        <div className="projects">
          {List.map((all) => {
            return (
              <div className="project" key={all.id}>
                <div className="img_container">
                  <img src={all.image} alt={all.title} />
                </div>
                <div className="details">
                  <Link to={all.url}>Check Now</Link>
                  <h3>{all.title}</h3>
                </div>
              </div>
            );
          })}
        </div>
      </motion.div>
    </>
  );
};

export default Projects;
