import React, { useState } from "react";

const AgeCalculator = () => {
  const Change = (e) => {
    const birthDay = new Date(e.target.value).getTime();
    const today = new Date().getTime();
    const diff = today - birthDay;

    const sec = Math.round(diff / 1000);
    const min = Math.round(diff / (1000 * 60));
    const hour = Math.round(diff / (1000 * 60 * 60));
    const day = Math.round(diff / (1000 * 60 * 60 * 24));
    const month = Math.round(diff / (1000 * 60 * 60 * 24 * 30.4375));
    const year = Math.round(diff / (1000 * 60 * 60 * 24 * 365.25));

    console.log(`${sec} ${min} ${hour} ${day} ${month} ${year}`);

    setYears(year);
    setMonths(month);
    setDays(day);
    setHours(hour);
    setMinutes(min);
    setSeconds(sec);

    document.querySelector(".age_results").style.visibility = "visible";
  };

  const [Years, setYears] = useState("");
  const [Months, setMonths] = useState("");
  const [Days, setDays] = useState("");
  const [Hours, setHours] = useState("");
  const [Minutes, setMinutes] = useState("");
  const [Seconds, setSeconds] = useState("");

  if (
    isNaN(Years) &&
    isNaN(Months) &&
    isNaN(Days) &&
    isNaN(Hours) &&
    isNaN(Minutes) &&
    isNaN(Seconds)
  ) {
    document.querySelector(".age_results").style.visibility = "hidden";
  }
  return (
    <div className="age_calculator">
      <h1>Age Calculator</h1>
      <input type="date" id="date-picker" onChange={Change} />
      <div className="age_results">
        <div id="years">
          <h1>Years</h1>
          <h3>{Years}</h3>
        </div>
        <div id="months">
          <h1>Months</h1>
          <h3>{Months}</h3>
        </div>
        <div id="days">
          <h1>Days</h1>
          <h3>{Days}</h3>
        </div>
        <div id="hours">
          <h1>Hours</h1>
          <h3>{Hours}</h3>
        </div>
        <div id="minutes">
          <h1>Minutes</h1>
          <h3>{Minutes}</h3>
        </div>
        <div id="seconds">
          <h1>Seconds</h1>
          <h3>{Seconds}</h3>
        </div>
      </div>
    </div>
  );
};

export default AgeCalculator;
