import { motion } from "framer-motion";
import React from "react";
import { pageTransitions, pageVariants } from "./Home";

const Err404 = () => {
  return (
    <motion.div
      initial="initial"
      animate="in"
      exit="out"
      variants={pageVariants}
      transition={pageTransitions}
    >
      <h1>404 Not Found!!</h1>
    </motion.div>
  );
};

export default Err404;
